import React from 'react';

const DayColumn = ({ day, appointments, openModal, toggleLock }) => {
  const dayString = day.toDateString();

  return (
    <div className="day-column" data-day={dayString} key={dayString}>
      <div className="day-header" onClick={() => openModal(day)}>
        {day.toLocaleDateString('it-IT', { weekday: 'long', day: 'numeric', month: 'long' })}
      </div>
      <div className="pills-container">
        {[...Array(24)].map((_, hour) => {
          const isBooked = appointments.some(appointment => {
            return (
              new Date(appointment.date).toDateString() === dayString &&
              hour >= appointment.start_hour &&
              hour <= appointment.end_hour
            );
          });

          return (
            <div
              className={`hour-pill ${isBooked ? 'locked' : 'unlocked'}`}
              key={hour}
              onClick={(e) => toggleLock(e, day)}
            >
              {`${hour}:00`}
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default DayColumn;
