import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../shared/supabaseClient';
import WidgetsModal from '../components/WidgetsModal';
import DayColumn from '../components/DayColumn';
import './bookme.css';

const BookMe = () => {
  const navigate = useNavigate();

  const [showModal, setShowModal] = useState(false);
  const [appointments, setAppointments] = useState([]);
  const [selectedDay, setSelectedDay] = useState(null);
  const [userId, setUserId] = useState(null);

  useEffect(() => {
    const loadUserAndAppointments = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        navigate('/signin');
        return
      }
      setUserId(user.id);

      let { data: Appointments, error } = await supabase
        .from('Appointments')
        .select("*")
        .eq('user_id', user.id)
        .gte('date', new Date().toISOString().split('T')[0]);

      if (error) {
        console.error("Errore nel caricamento degli appuntamenti:", error);
      } else {
        setAppointments(Appointments);
      }
    };

    loadUserAndAppointments();
  }, []);

  const getNextDays = () => {
    const days = [];
    const today = new Date();
    for (let i = 0; i < 7; i++) {
      const date = new Date(today);
      date.setDate(today.getDate() + i);
      days.push(date);
    }
    return days;
  };

  const createCalendar = () => {
    const days = getNextDays();

    return days.map(day => (
      <DayColumn
        key={day.toDateString()}
        day={day}
        appointments={appointments}
        openModal={openModal}
        toggleLock={toggleLock}
      />
    ));
  };

  const openModal = (day) => {
    if (!day) {
      console.error("Tentativo di aprire il modal senza selezionare un giorno.");
      return;
    }
    setSelectedDay(day);
    setShowModal(true);
  };

  const toggleLock = async (e, day) => {
    const pill = e.target;

    if (!day) {
      console.error("Nessun giorno selezionato.");
      return;
    }

    const hour = parseInt(pill.innerText.split(':')[0], 10);
    const date = day.toISOString().split('T')[0];

    if (pill.classList.contains('locked')) {
      // Rimuovi appuntamento
      const { error } = await supabase
        .from('Appointments')
        .delete()
        .eq('date', date)
        .eq('start_hour', hour)
        .eq('user_id', userId);

      if (error) {
        console.error("Errore nella rimozione dell'appuntamento:", error);
      } else {
        pill.classList.remove('locked');
        pill.classList.add('unlocked');
        setAppointments(prev => prev.filter(appt => appt.date !== date || appt.start_hour !== hour));
      }
    } else {
      // Aggiungi appuntamento
      const { data, error } = await supabase
        .from('Appointments')
        .insert([{ date, start_hour: hour, end_hour: hour, user_id: userId }]);

      if (error) {
        console.error("Errore nella creazione dell'appuntamento:", error);
      } else {
        // Aggiorna lo stato localmente senza attendere la risposta
        pill.classList.remove('unlocked');
        pill.classList.add('locked');

        // Aggiungi l'appuntamento localmente per aggiornare immediatamente l'interfaccia utente
        const newAppointment = { date, start_hour: hour, end_hour: hour, user_id: userId };
        setAppointments(prev => [...prev, newAppointment]);
      }
    }
  };

  return (
    <div id="calendar-body">
      {showModal && (
        <WidgetsModal setShowModal={setShowModal} selectedDay={selectedDay} />
      )}

      <div id="calendar">
        {createCalendar()}
      </div>
    </div>
  );
};

export default BookMe;
